              </div>
              <!-- :class="{spanIsActive:index == current}" -->
              <div class="reschedule__modal__right__coach-list">
                <div
                  @click="clickedCoach(data, index)"
                  v-for="(data, index) in GET_CoachResult.coach_result"
                  :key="index"
                  v-model="newCoach = currentCoach"
