                     </div>
                     <div class="coach__profile__rating">
                        <v-rating :value="Number(data.coach_rating)" color="yellow darken-3" readonly small half-increments></v-rating>
                     </div>
                     <div class="coach__profile__tokens">
                        {{ data.coach_cost }} Tokens
                     </div>
                     <div class="scheduleinfo btnSqure" @click="seeProfile(data.username)">
                        <router-link to="/student/view" target="_blank">See Profile</router-link>
                     </div>
