                  readonly
                  @click="togleDatepicker"
                ></v-text-field>
                <v-date-picker
                  v-model="date"
                  v-if="menu"
                  no-title
                  :min="getStartEndTime.start"
                  :max="getStartEndTime.end"
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="menu = false">OK</v-btn>
                </v-date-picker>
              </div>
            </div>
            <div class="reschedule__modal__right__time">
              <span>Coach Available Time</span>
              <div class="modal__time">
                <div class="BxBookCoach__CoachDetail__time">
                   <div class="timepicker">
                      <div class="bxEvent">
                        <div v-if="showLottie" style="margin-top:25px;">
                          <lottie :options="defaultOptions" :height="100" :width="120"/>
                        </div>
                        <div class="events" v-if="!coachCheckRescheduleData && !showLottie">
                          <h4>No Available Time</h4>
                        </div>
                        <div class="bxDate" v-if="coachCheckRescheduleData && !showLottie">
