                <div class="tooltip--server mobile" v-show="show">
                  <p>By rescheduling appointments youre agree to these points.</p>
                  <ul>
                    <li>We will remind 24 hours before session appointment begins.</li>
                    <li>You still can change the appointment 1 hours after you receive an email reminder from us.</li>
                    <li>If the appointment still on and youre not coming, you cannot refund the tokens.</li>
                  </ul>
                </div>
