                No summary
              </div>
              <div class="box__reschedule__summary" v-if="toggleSummary">
                <div class="reschedule__summary--left">
                  <h5>From</h5>
                  <div class="flex--space-between">
                    <span>(UTC +7)</span><span>{{ filteredDate }}</span>
                  </div>
                  <h3>{{ filteredStart }} - {{ filteredEnd }}</h3>
                  <h4>{{ sessionDetail.user_data[0].fullname }}</h4>
                </div>
                <div class="reschedule__summary--right">
                  <h5>To</h5>
                  <div class="flex--space-between">
                    <span>(UTC +7)</span><span>{{ filteredDateSummary }}</span>
                  </div>
                  <h3>{{ this.coachCheckRescheduleTime.coach_schedule[this.currentTime] }}</h3>
                  <h4 v-if="!checkCoachResult">{{ sessionDetail.user_data[0].fullname }}</h4>
                  <h4 v-if="checkCoachResult">{{ this.GET_CoachResult.coach_result[this.newCoach].name }}</h4>
                </div>
              </div>
