<template lang="html">
  <div class="session__resp--list">
    <div class="session__resp--list-each" v-for="(data, i) in sortedItems" :key="i">
      <div class="session__resp--list-collapsed">
        <div class="session__resp--list-left">
          <div class="session__resp--list-time">{{ data.start_time | formatTime }} - {{ data.end_time | formatTime }}</div>
          <div class="session__resp--list-date">{{ data.date | formatDateDay }}</div>
          <div class="session__resp--list-desc overflow--ellipsis">Session with {{ data.coach_name }}</div>
        </div>
      </div>
      <div class="semi__btn center">Check Availibility</div>
    </div>
  </div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'

export default {
  components: {

  },
  mixins: [MomentFilter],
  data: () => ({
    historySessions: [],
  }),
  created () {
    axios({
      method: 'GET',
      url: '/session/history/',
      headers: {Authorization: localStorage.getItem('token')}
     })
     .then(res => {
       console.log(res);
       this.historySessions = res.data.new_appointment
     })
     .catch(function (error) {
       console.log(error);
     });
  },
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.date < b.date)
          return 1;
        if (a.date > b.date)
          return -1;
        return 0;
      }

      const data = this.$store.state.SessionHistory.new_appointment

      if (data == null) {
        return 0
      } else {
        return data.sort(compare);
      }
    },
    sessionListCheck () {
      if (this.sortedItems != 0) {
        return true
      } else {
        return false
      }
    },
  },
  methods: {
    // getCoach (i, username, appointment_id) {
    //   const coachDetail = {
    //     username : username,
    //     app_id : appointment_id
    //   }
    //   this.$store.dispatch('POST_SESSION_DETAIL', coachDetail)
    // },
  },
}
</script>

<style lang="css">
</style>



