<template lang="html">
	<div class="table__list">
		<div class="NocoachAvailable--global" v-if="!sessionListCheck">
			<div class="NoAvailable">
				<p>There's no history session</p>
			</div>
		</div>
		<table style="width:100%" v-if="sessionListCheck">
			<tr>
			<th>Transaction</th>
			<th>Coach</th>
			<th>Previous Coach</th>
			<th>Session Date</th>
			<th>Recorded Session</th>
			</tr>
			<tr v-for="(data, i) in sortedItems">
			<td align="center">{{ data.created | formatDate }}</td>
			<td align="center">{{ data.coach_name }}</td>
			<td align="center"> - </td>
			<td align="center">{{ data.date | formatDate }}, {{ data.start_time | formatTime }} - {{ data.end_time | formatTime }}</td>
			<td>
				<div class="semi__btn" @click="changeDialogState(data)">Check Availibility</div>
			</td>
			</tr>
		</table>
	</div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'
import axios from '../../../store/axios-auth'

export default {
	components:{
	
	},
	mixins: [MomentFilter],
	data () {
		return {
			historySessions: [],
			dialogVideo:false
		}
	},
	computed: {
		sortedItems() {
			function compare(a, b) {
				if (a.date < b.date)
					return 1;
				if (a.date > b.date)
					return -1;
				return 0;
			}

			const data = this.$store.state.SessionHistory.new_appointment

			if (data == null) {
				return 0
			} else {
				return data.sort(compare);
			}
		},
		sessionListCheck () {
			if (this.sortedItems != 0) {
				return true
			} else {
				return false
			}
		}
	},
	mounted() {
		console.log(this.sortedItems)
	},
	methods: {
		changeDialogState (data) {
			if (data.type == 0) {
				this.methodOpentok(data)
			} else {
				this.methodAgora(data)
			}

		},
		methodOpentok (data) {
			this.$store.commit('toggleDialogTwo');
			const dataReq = {
				session : data.session,
				session_type : 'Opentok'
			}
			this.$store.dispatch('POST_SESSION_ID', dataReq)
			// console.log("stuOpentok");
		},
		methodAgora (data) {
			const dataReq={
				session : data.session,
				session_type : 'Agora'
			}
			this.$store.dispatch('POST_SESSION_ID', dataReq)

			// window.open("/student/video/", '_blank');
			this.$store.commit('toggleDialogThree', true);
		}
	},

}
</script>

<style lang="scss">
</style>



